/*global __PREFIX_PATHS__, __PATH_PREFIX__ */
import React from 'react'
import Link, { withPrefix } from 'gatsby-link'
import Nav from '../components/Nav'
import '../../static/main.scss'
import { getPageSpecificClass } from '../utils/getDataFromPath'

import { rhythm, scale } from '../utils/typography'

const Template = ({ location, children, data: layoutData }) => {
  let header
  let rootPath = `/`
  if (!layoutData) {
    return <p>Nothing here!</p>
  }
  const navPaths = layoutData.pages.edges.map(edge => edge.node.path)
  //console.log('navPaths', navPaths)
  const navData = layoutData.pagesMarkdown.edges.map(edge => {
    const path = navPaths.filter(
      navPath =>
        edge.node.fileAbsolutePath.indexOf(navPath) !== -1 && navPath !== '/'
    )[0]
    const title = edge.node.frontmatter.navTitle
      ? edge.node.frontmatter.navTitle
      : edge.node.frontmatter.title
    return {
      title,
      path,
    }
  })

  const pageLayoutClass = getPageSpecificClass(location)

  if (withPrefix) {
    rootPath = withPrefix(rootPath)
  }
  let isHome = false
  let typistIncompleteClass = 'typist-incomplete'

  isHome = location.pathname === rootPath
  console.debug(`isHome: ${isHome}; location.pathname: ${location.pathname}; rootPath: ${rootPath}`)

  if (isHome) {
    header = (
      <div className="header header_home">
        <Nav items={navData} classNames="header__nav" />
      </div>
    )
  } else {
    typistIncompleteClass = ''
    header = (
      <div className="header header_page">
        <h3
          style={{
            fontFamily: 'Montserrat, sans-serif',
            marginTop: 0,
            marginBottom: rhythm(-1),
          }}
        >
          <Link
            style={{
              boxShadow: 'none',
              textDecoration: 'none',
              color: 'inherit',
            }}
            to={'/'}
          >
            {layoutData.site.siteMetadata.title}
          </Link>
        </h3>
        <Nav items={navData} classNames="header__nav" />
      </div>
    )
  }
  return (
    <main
      className={`layout main layout_${pageLayoutClass} ${typistIncompleteClass}`}
      style={{
        maxWidth: rhythm(24),
        minWidth: rhythm(14),
        padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
      }}
    >
      {header}
      {children()}
    </main>
  )
}

export default Template

// Return markdown title and navTitle for pages only, and not the root index page, along with page paths
// Working exclusion regex : regex: "/^(?!.*(\/(posts|partials)))/",
// but with reorgz, should be able to do inclusion of pages only
export const templateQuery = graphql`
  query TemplateQuery {
    pagesMarkdown: allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "//pages/", ne: "/" } }
    ) {
      edges {
        node {
          fileAbsolutePath
          frontmatter {
            title
            navTitle
          }
        }
      }
    }
    pages: allSitePage {
      edges {
        node {
          path
        }
      }
    }
    site {
      siteMetadata {
        title
      }
    }
  }
`



